<template>
    <div class="streetcontainer">
      
      <div class="main">
        <div id="place">
          <div class="placetitle">
            【{{city.cityname }}】{{cityMap.placename }}{{cityMap.id }}
          </div>
          <div class="placedesc">
            {{cityMap.placedesc }}
          </div>
        </div>
      
        <div class="npc">
          <div id="talkshow" onclick="hiddentalkshow();">
            <div id="talkshowclose">
              <a onclick="hiddentalkshow();">X</a>
            </div>
            <div id="talkshowdetail">
              
            </div>
          </div>

          <div v-for="npc in npclist">
            <div class="npcdiv" :id="'npc_' + npc.npcid " @mouseup="showDiv('npcdesc_' + npc.npcid);" @mouseout="hiddenDiv('npcdesc_' + npc.npcid);">
              <img :alt="'a' + npc.name " :title="'t' + npc.name " :src="require('../../assets/fantasy/' + npc.facesrc)">
              <div :id="'npcdesc_' + npc.npcid " v-show="npcdesc == 'shownpcdesc_' + npc.npcid" class="npcdescdiv" @mouseover="mouseoverDiv('npcdesc_' + npc.npcid)" @mouseout="mouseoutDiv('npcdesc_' + npc.npcid);">
                <div id="npctitle">
                  {{npc.name }}
                </div>
                <div id="npcdesc">
                  {{npc.desc }}
                </div>
                <div id="npcbutton">
                  <div id="talktonpc" @click="talkshow(npc.npcid);">
                    交谈
                  </div>
                  <div id="asktonpc" @click="hiddenThis('npcdesc_' + npc.npcid );alert('询问');">
                    询问
                  </div>
                  <div id="applyfromnpc">
                    接受任务
                  </div>
                  <div id="passtonpc">
                    给道具
                  </div>
                  <div id="refusetask">
                    推任务
                  </div>
                </div>
              </div>
            </div>
          </div>
            
        </div>
        <div class="groundtitle">
          地上的道具
        </div>
        
        <div class="groundTips">
          点击地上的道具可以将它收入囊中哦！
        </div>
        <div class="ground">
          <div class="groundLayer">
            <div v-show="itemList == null" v-for="n in 30">
              <div class="groundItems">
              </div>
              <div v-if="n % 10 == 0" style="clear:both"></div>
            </div>
          </div>
          <div class="groundLayer">
            <div v-show="itemList != null" v-for="(item,index) in itemList">
              <div class="groundItems">
                <a v-if="item != null" :id="'item_' +index" @click="pickitem(cityMap.id, index);" ref="item">
                  <img :alt="'a' + item.itemName" :title="'t' + item.itemName" :src="require('../../assets/fantasy/' + item.imgSrc)">
                </a>
              </div>
              <div v-if="index % 10 == 9" style="clear:both"></div>
            </div>
          </div>
        </div>
      </div>

      <div class="sidebox">
        <select id="userlist" name="userlist"  class="selectStyle" multiple="multiple">
          <option v-for="user in users" :value="user.id">{{user.name}}</option>
        </select>
        <div class="event">
          <div class="eventtitle">
            特殊事件
          </div>
          <div class="eventitem">
            <div v-show="cityMap.id == user.homePoint" id="gohome">
              <a @click="toHome"><font>回家</font></a>
            </div>
          </div>
        </div>
        <div class="quickmove">
          <div class="quickmovetitle">
            快速移动
          </div>
          <div class="quickmoves">
            <a @click="toHome"><font>回家</font></a>
          </div>
        </div>
      </div>
        
    </div>
</template>

<style src="../../styles/fantasy-street.css"></style>
<script>
import {postRequest} from '../../utils/api'
import {base} from '../../utils/api'
export default {
  data() {
    return {
      cityMap: {
        id: "",
        placename: "",
        cityname: "",
        placedesc: "",
        north: 0,
        northeast: 0,
        east: 0,
        southeast: 0,
        south: 0,
        southwest: 0,
        west: 0,
        northwest: 0,

      },
      city: {
        id: "",
        cityname: ""
      },
      user: {
        id: ""
      },
      npclist: {

      },
      itemList: {
        
      },
      users: {

      },
      npcdesc: "",
      fromid: null,
      targetid: null,
      items: {}
    };
  },
  beforeRouteUpdate(to, from, next){
    vm.fromid = to.query.fromid;
    vm.targetid = to.query.targetid;
    next(this.initCityMap(vm.fromid, vm.targetid));
  },
  mounted: function () {
    this.initCityMap();
    
    this.$root.eventHub.$on('items', this.updateItemList);
  },
  created: function () {
    
  },
  beforeDestroy: function () {
    
    this.$root.eventHub.$off('items', this.updateItemList);
  },
  methods: {
      updateItemList: function(items) {
        //console.log(items)
        this.itemList = items;
        //Vue.set(this, 'itemList', items);
      },
      initCityMap: function(fromid, targetid) {
        //alert(fromid == null)
        //alert(vm.targetid)
        //刷新页面时，需要从url中获取节点id
        if (fromid == null) {
          fromid = this.$route.query.fromid;
          targetid = this.$route.query.targetid;

        }
        postRequest('/main/moveTo', {
          fromid: fromid, 
          targetid: targetid
          }).then(resp=> {
            if (resp.status == 200) {
              //成功
              var data = resp.data;
              //alert(JSON.stringify(data))
              //alert(data.result == false)
              if (data.result == true) {
                this.cityMap = data.cityMap;
                this.city = data.city;
                this.user = data.user;
                this.npclist = data.npclist;
                this.users = data.users;
                this.itemList = data.itemList;

                this.$root.eventHub.$emit('cityMap', this.cityMap);
              } else {
                this.$alert(data.message, '失败!');
                this.$router.replace({path: '/'});
              }
            } else {
              //失败
              this.$alert('用户非法⊙﹏⊙∥!', '失败!');
              this.$router.replace({path: '/'});
            }
          }, resp=> {
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
            this.$router.replace({path: '/'});
          });
      },
      
      toHome: function() {
        this.$router.replace({path: '/home'});
      },

      
      showDiv: function(npcdesc) { 
        this.npcdesc = "show" + npcdesc;
        //npcdesc.style.display = "block";
      },
      //当鼠标移动到目标div的子元素上时，会触发子元素的mouseoverDiv方法，设置inDiv = true，这样延迟后的判断条件就不成立，从而继续显示了。
      hiddenDiv: function(npcdesc) {
        this.npcdesc = "hidden" + npcdesc;
      },

      mouseoverDiv: function(npcdesc) { 
        this.npcdesc = "show" + npcdesc;
      }, 
      mouseoutDiv: function(npcdesc) {
        this.npcdesc = "hidden" + npcdesc;
      },
      pickitem: function(mapId, itemId) {
        //alert(mapId + ", " + itemId);
        postRequest('/item/pickItem', {
          mapId: mapId, 
          itemId: itemId
          }).then(resp=> {
            if (resp.status == 200) {
              //成功
              var data = resp.data;
              //alert(JSON.stringify(data))
              //alert(data.result == false)
              if (data.result == true) {
                //拾取成功后，隐藏该道具
                //v-for循环出来的集合，不能在页面上即时更新，需要操作完后调用vm.$set(key,value)刷新页面元素 wanghui 2018.10.1
                this.itemList[itemId] = null;
                vm.$set(this.itemList, this.itemList);
                //this.$refs.item[itemId].remove()//.style.visibility = "hidden";
                //不能删list，删后会造成格子减少
                //for(var i=0; i<this.itemList.length; i++) {
                //  if (i == itemId) {
                //    this.itemList.splice(i, 1);
                //  }
                //}
                
                this.$alert("道具拾取成功");
                //console.log(this.$refs.item[itemId].style)
              } else {
                if (data.message == "noitem") {
                  this.$alert("一阵残影闪过，你眼睁睁的看着快到手的鸭子飞走了~~~", '失败!');
                  //因此处道具不存在拾取失败后，隐藏该道具.children[0].remove()//
                  this.itemList[itemId] = null;
                  vm.$set(this.itemList, this.itemList);
                  //this.$refs.item[itemId].children[0].children[0].style.visibility = "hidden";
                } else {
                  this.$alert(data.message, '失败!');
                }
                //this.$router.replace({path: '/'});
              }
            } else {
              //失败
              this.$alert('用户非法⊙﹏⊙∥!', '失败!');
              //this.$router.replace({path: '/'});
            }
          }, resp=> {
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
            //this.$router.replace({path: '/'});
          });
      }
  }
}
</script>

<style>

</style>


