<template>
    <div>
      <div id="userzone">
				<div id="userface">
					<img src="../../assets/fantasy/face.jpg" border="0">
				</div>
				<div id="compass">
					<div id="northwest" ref="northwest" @click="moveto(cityMap.northwest);"></div>
					<div id="north" ref="north" @click="moveto(cityMap.north);"></div>
					<div id="northeast" ref="northeast" @click="moveto(cityMap.northeast);"></div>
					<div id="west" ref="west" @click="moveto(cityMap.west);"></div>
					<div id="center" ref="center" @click="moveto(cityMap.center);"></div>
					<div id="east" ref="east" @click="moveto(cityMap.east)"></div>
					<div id="southwest" ref="southwest" @click="moveto(cityMap.southwest);"></div>
					<div id="south" ref="south" @click="moveto(cityMap.south);"></div>
					<div id="southeast" ref="southeast" @click="moveto(cityMap.southeast);"></div>
				</div>
			</div>
			<div id="functions">
				<a @click="openpacket">
					<font>打开背包</font>
				</a>
				<a @click="savequit">
					<font>存盘退出</font>
				</a>
			</div>

      
      <el-dialog title="我的背包" :visible.sync="dialogTableVisible" :before-close="ai_dialog_close" >
          
        <div id="wrap">
          <div id="itemtitle">
            我的物品<label>（{{itemCount}}/30）</label>
          </div>
          <div id="itemlist">
            <div v-show="packetItems != null" v-for="(item,index) in packetItems" v-bind:key="index" ref="item"> 
              <input :id="item.itemId" type="checkbox" :value="item.itemId"/>
              <label :for="item.itemId">
                <img :alt="'a' + item.itemName" :title="'t' + item.itemName" :src="require('../../assets/fantasy/' + item.imgSrc)">
                {{item.itemName}}
              </label>
              <a @click="useitem(item.itemId);">使用</a>
              <a @click="dropitem(item.itemId, index);">扔掉</a>
              <a @click="delitem(item.itemId, index);">删除</a>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
</template>
<script>
  import {getRequest} from '../../utils/api'
import {postRequest} from '../../utils/api'
export default {
  data() {
    return {
      cityMap: {
        id: "",
        placename: "",
        cityname: "",
        placedesc: "",
        north: 0,
        northeast: 0,
        east: 0,
        southeast: 0,
        south: 0,
        southwest: 0,
        west: 0,
        northwest: 0,

      },
      dialogTableVisible: false,
      packetItems: {},
      itemCount: 0,
    };
  },
  created: function () {
    this.$root.eventHub.$on('cityMap',this.initCompass);
  },
  methods: {
      initCompass: function(cityMap) {
        //alert(JSON.stringify(cityMap));
        this.cityMap.id = cityMap.id;
        if (cityMap.northwest != null && cityMap.northwest != 0) {
          this.$refs.northwest.style.display = 'block';
          this.cityMap.northwest = cityMap.northwest;
        } else {
          this.$refs.northwest.style.display = 'none';
        }
        if (cityMap.north != null && cityMap.north != 0) {
          this.$refs.north.style.display = 'block';
          this.cityMap.north = cityMap.north;
        } else {
          this.$refs.north.style.display = 'none';
        }
        if (cityMap.northeast != null && cityMap.northeast != 0) {
          this.$refs.northeast.style.display = 'block';
          this.cityMap.northeast = cityMap.northeast;
        } else {
          this.$refs.northeast.style.display = 'none';
        }
        
        if (cityMap.west != null && cityMap.west != 0) {
          this.$refs.west.style.display = 'block';
          this.cityMap.west = cityMap.west;
        } else {
          this.$refs.west.style.display = 'none';
        }
        if (cityMap.east != null && cityMap.east != 0) {
          this.$refs.east.style.display = 'block';
          this.cityMap.east = cityMap.east;
        } else {
          this.$refs.east.style.display = 'none';
        }

        if (cityMap.southwest != null && cityMap.southwest != 0) {
          this.$refs.southwest.style.display = 'block';
          this.cityMap.southwest = cityMap.southwest;
        } else {
          this.$refs.southwest.style.display = 'none';
        }
        if (cityMap.south != null && cityMap.south != 0) {
          this.$refs.south.style.display = 'block';
          this.cityMap.south = cityMap.south;
        } else {
          this.$refs.south.style.display = 'none';
        }
        if (cityMap.southeast != null && cityMap.southeast != 0) {
          this.$refs.southeast.style.display = 'block';
          this.cityMap.southeast = cityMap.southeast;
        } else {
          this.$refs.southeast.style.display = 'none';
        }
        //走到居住区时，显示回家按钮
        // if ("${user.homePoint}" == "${cityMap.id}") {
        //   document.getElementById("gohome").style.display = "block";	
        // }
      },
      moveto: function(id) {
        //alert(JSON.stringify(id));
        //alert(this.cityMap.id);
        //this.$router.go(0);
        this.$router.push({path: '/main/street', query: { fromid: this.cityMap.id, targetid: id }});
        //this.$router.push({path: '/main/street'});
      },
      savequit: function(){
        var _this = this;
        this.$confirm('您确定要存盘退出吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function () {
          getRequest("/login/logout")
          _this.$router.replace({path: '/'});
        }, function () {
          //取消
        })
      },
      ai_dialog_close(){
        this.dialogTableVisible = false;
      },
      //打开背包
      openpacket: function(){
        this.dialogTableVisible = true;
        getRequest('/item/openPacket', {
        }).then(resp=> {
          if (resp.status == 200) {
            //成功
            var data = resp.data;
            //console.log(data.packetItems);
            //alert(JSON.stringify(data.packetItems))
            //alert(data.result == false)
            if (data.result == true) {
              this.packetItems = data.packetItems;
              this.itemCount = this.packetItems.length;
            } else {
              this.$alert(data.message, '失败!');
            }
            //alert(JSON.stringify(this.packetItems))
          } else {
            //失败
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
          }
        }, resp=> {
          this.$alert('用户非法⊙﹏⊙∥!', '失败!');
        });
      },
      //丢掉道具
      dropitem: function(itemId, index){
        postRequest('/item/dropItem', {
          mapId: this.cityMap.id,
          itemId: itemId
        }).then(resp=> {
          if (resp.status == 200) {
            //成功
            var data = resp.data;
            //alert(JSON.stringify(data))
            //alert(data.result == false)
            if (data.result == true) {
              this.items = data.items;
              this.itemCount--;
              this.$refs.item[index].style.display = "none";
              this.$root.eventHub.$emit('items', this.items);
            } else {
              this.$alert(data.message, '失败!');
            }
          } else {
            //失败
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
          }
        }, resp=> {
          this.$alert('用户非法⊙﹏⊙∥!', '失败!');
        });
      },
      //删除道具
      delitem: function(itemId, index){
        postRequest('/item/deleteItem', {
          itemId: itemId
        }).then(resp=> {
          if (resp.status == 200) {
            //成功
            var data = resp.data;
            //alert(JSON.stringify(data))
            //alert(data.result == false)
            if (data.result == true) {
              this.itemCount--;
              this.$refs.item[index].style.display = "none";
            } else {
              this.$alert(data.message, '失败!');
            }
          } else {
            //失败
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
          }
        }, resp=> {
          this.$alert('用户非法⊙﹏⊙∥!', '失败!');
        });
      }
  },
  beforeDestroy: function() {
    this.$root.eventHub.$off('cityMap',this.initCompass);
  }
}
</script>


<style>
  label img {
    height: 52px;
    width: 52px;
  }
</style>


