<template>
    <div id="chatWrap">
        <input type="hidden" id="fromUser" />
        <ul id="con">
            <li id="city" class="show"></li>
            <li id="world"></li>
            <li id="trade"></li>
            <li id="whisper"></li>
            <li id="system"></li>
        </ul>
        <form id="sangForm">
            <div class="func">
                <span id="lock" :class="lockClass" @click="lockit();">{{lockStr}}</span>
                <select id="msgType" name="msgType">
                    <option value="城市">城市</option>
                    <option value="世界">世界</option>
                    <option value="交易">交易</option>
                    <option value="私聊">私聊</option>
                </select>
            </div>
            <div id="tit">
                <span
                    v-for="tab in tabs"
                    v-bind:key="tab"
                    v-bind:class="[{ select: currentTab === tab }]"
                    v-on:click="titit(tab)"
                >{{tab}}</span>
                <!-- <span class="select" @click="titit(this)">城</span>
                <span @click="titit(this)">世</span>
                <span @click="titit(this)">交</span>
                <span @click="titit(this)">私</span>
                <span @click="titit(this)">系</span> -->
            </div>
            <input type="text" id="text" name="text"/>
            <input type="button" @click="talk" value="发送"/>
        </form>
    </div>
</template>

<style src="../styles/fantasy-chat.css"></style>
<style>

</style>

<script>
    import {postRequest} from '../utils/api'
    import {getRequest} from '../utils/api'
    import {chatUrl} from '../utils/api'
    import SockJS from "sockjs-client"
    import Stomp from "stompjs"
    import $ from 'jquery'
    export default {
        data() {
            return {
                lockScroll: false,
                lockClass: '',
                lockStr: '锁',
                fromUser: '',
                stomp: null,
                currentTab: '城',
                tabs: ['城', '世', '交', '私', '系'],
                currentUser: null,

            };
        },
        mounted: function () {
            this.disconnect();
            this.connect();
            this.$root.eventHub.$on('currentUser', this.connect);
        },
        created: function () {
        },
        beforeDestroy: function () {
            this.disconnect();
            this.$root.eventHub.$off('currentUser', this.connect);
        },
        methods: {
            disconnect: function() {
                if(this.stomp != null && this.stomp.connected){
                    this.stomp.disconnect();
                    console.log("Disconnected");
                }
            },
            connect: function(currentUser) {
                let _this = this;
                console.log(currentUser)
                if (this.currentUser == null) {
                    this.currentUser = currentUser;
                }
                console.log(this.currentUser)
                if (this.stomp != null || this.currentUser == null) {
                    return ;
                }
                let socket = new SockJS(chatUrl + 'webSocketServer');
                //console.log(socket)
                this.stomp = Stomp.over(socket);
                //console.log(this.stomp)
                if(this.stomp != null && !this.stomp.connected){
                    this.stomp.connect(
                    {
                        name: this.currentUser.id // 携带客户端信息
                    },
                    function (frame) {
                        //发送给指定用户的，都需要以/user开头
                        _this.stomp.subscribe("/trade/getResponse", _this.tradeHandleNotification);
                        _this.stomp.subscribe("/user/whisper/getResponse", _this.whisperHandleNotification);
                        _this.stomp.subscribe("/user/city/getResponse", _this.cityHandleNotification);
                        _this.stomp.subscribe("/world/getResponse", _this.worldHandleNotification);
                        _this.stomp.subscribe("/system/getResponse", _this.systemHandleNotification);
                        _this.stomp.subscribe("/user/system/getResponse", _this.systemHandleNotification);
                    });
                }
            },
            lockit: function() {
                //锁定滚动条，不再自动滚动到最底部
                if (this.lockScroll == false) {
                    this.lockScroll = true;
                    this.lockStr = "解";
                    this.lockClass = 'select';
                } else {
                    this.lockScroll = false;
                    this.lockStr = "锁";
                    this.lockClass = '';
                    this.keepBottom($('#con li.show'));
                }
            },
            //切换聊天窗口
            titit: function(tab) {
                this.currentTab = tab;
                var i = this.tabs.indexOf(tab);
                $('#con li').eq(i).show().siblings().hide();
                this.keepBottom($('#con li').eq(i));
            },

            //保持滚动条在最底部
            keepBottomAll: function() {
                this.keepBottom($('#city'));
                this.keepBottom($('#world'));
                this.keepBottom($('#trade'));
                this.keepBottom($('#whisper'));
            },
            keepBottom: function(element) {
                if (this.lockScroll == false) {
                    element.scrollTop(element[0].scrollHeight);
                }
            },
            //根据消息类型，获取间隔时间
            getTalksTime: function(msgType) {
                var TALKS_WORLD_TIME = 60*1000;
                var TALKS_CITY_TIME = 5*1000;
                var TALKS_TRADE_TIME = 5*1000;
                var TALKS_WHISPER_TIME = 5*1000;
                var talksTime = TALKS_WORLD_TIME;
                if (msgType == "城市") {
                    talksTime = TALKS_CITY_TIME;
                } else if (msgType == "世界") {
                    talksTime = TALKS_WORLD_TIME;
                } else if (msgType == "交易") {
                    talksTime = TALKS_TRADE_TIME;
                } else if (msgType == "私聊") {
                    talksTime = TALKS_WHISPER_TIME;
                }
                return talksTime;
            },
            //接收城市频道消息，并切换到城市频道
            cityHandleNotification: function(message) {
                var cls = "";
                if (message.body.indexOf("【系统】") == 0) {
                    cls = "system";
                } else {
                    cls = "city";
                }
                var styleMsg = "<span class='" + cls + "'>" + message.body + "</span>";
                $("#city").append(styleMsg).append("<br/>");
                //alert($("#city").scrollTop)
                //alert($("#city").scrollHeight)
                this.keepBottomAll();
                //keepBottom($('#city'));

            },
            //接收世界频道消息，并切换到世界频道
            worldHandleNotification: function(message) {
                var styleMsg = "<span class='world'>" + message.body + "</span>";
                $("#world").append(styleMsg).append("<br/>");
                this.keepBottomAll();
                //keepBottom($('#world'));
            },
            //接收交易频道消息，并切换到交易频道
            tradeHandleNotification: function(message) {
                var styleMsg = "<span class='trade'>" + message.body + "</span>";
                $("#trade").append(styleMsg).append("<br/>");
                this.keepBottomAll();
                //keepBottom($('#trade'));
            },
            //接收私聊频道消息
            whisperHandleNotification: function(message) {
                var styleMsg = "<span class='whisper'>" + message.body + "</span>";
                $("#whisper").append(styleMsg).append("<br/>");
                $("#city").append(styleMsg).append("<br/>");
                this.keepBottomAll();
                //keepBottom($('#whisper'));
            },
            //接收系统频道消息
            systemHandleNotification: function(message) {
                var styleMsg = "<span class='system'>" + message.body + "</span>";
                $("#system").append(styleMsg).append("<br/>");
                this.keepBottomAll();
                //keepBottom($('#system'));
            },
            sendSpittle: function(msgType, text) {
                if (msgType == "城市") {
                    this.stomp.send("/city", {cityId:1}, text);
                } else if (msgType == "世界") {
                    this.stomp.send("/world", {}, text);
                } else if (msgType == "交易") {
                    this.stomp.send("/trade", {}, text);
                } else if (msgType == "私聊") {
                    this.stomp.send("/whisper", {}, text);
                }
            },
            talk: function() {
                var text = $("#text").val()//.replace(/[~'!<>@#$%^&*()-+_=:]/g, "");
                var msgType = $("#msgType").val();
                var talksTime = this.getTalksTime(msgType);
                if (this.disabled == true) {
                    alert("请等待" + talksTime/1000 + "秒钟再发送消息。");
                    $("#text").focus();
                    return false;
                }

                if (text == "") {
                    alert("信息不能为空！");
                    return false;
                }
                if (text.length > 200) {
                    alert("信息过长，请分割后发送！");
                    return false;
                }
                //如果消息是以/开头的话，可能是私聊信息，切换类型到私聊
                if (text.indexOf("/") == 0) {
                    $("#msgType").val("私聊");
                    msgType = $("#msgType").val();
                }

                this.sendSpittle(msgType, text);
                $("#text").val("");
                $("#text").focus();

                this.disabled = true;
                var self = this;
                function recover() {
                    self.disabled = false;
                }
                setTimeout(recover, talksTime);
                setInterval(recover, talksTime);
            }





        }   
    }
</script>



