import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/Login'
import Index from '@/pages/Index'
import homemain from '../components/main/homemain.vue'
import streetmain from '../components/main/streetmain.vue'
import homeleft from '../components/left/homeleft.vue'
import streetleft from '../components/left/streetleft.vue'

Vue.use(Router)
export default new Router({
  mode: 'history',
  base: '/vuefantasy/', //发布要加上这一行
  routes: [
    {
      path: '/',
      name: '登录',
      hidden: true,
      component: Login
    }, 
    {
      path: '/main',
      name: '',
      component: Index,
      children: [
        {
          path:'/',
          //component:homemain,
          components: {
            default: homemain,
            left: homeleft
          }
        },
        {
          path:'/home',
          //component:homemain,
          components: {
            default: homemain,
            left: homeleft
          }
        },
        {
          path: 'street',
          //component: streetmain,
          components: {
            default: streetmain,
            left: streetleft
          },
          
        },
      // {
      //   path: 'street',
      //   components: {
      //     main: streetmain
      //   }
      // }
      ]
      
    }, 
    // {
    //   path: '/street',
    //   name: '',
    //   //component: Home,
    //   components: {
    //     default: Home,
    //     main: streetmain
    //   },
    //   hidden: true
    // }
  ]
})



