import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// import './styles/element-variables.scss'
import './utils/filter_utils.js'

Vue.use(ElementUI)
Vue.config.productionTip = false
window.bus = new Vue();
var vm = new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: {App},
  data: {
    eventHub: new Vue()
  }
})
global.vm = vm;



