        border-left: 30px solid #fff;
    }
    .fantasy-tips {
        width:100%;
        height:30px;
        text-align: right;
        color:#ff0;
    }
    .nav {
        list-style-type: none;
        margin: 10px 10px;
        text-align: center;
        font-size: 16px;
    }
    .nav li {
        float: left;
        width: auto;
        padding: 6px;
    }
    .nav li a {
        color:#fff;
        display: block;
    }
    .nav li a:hover {
        text-decoration:underline;
        color:#FF0000
    }
    .nav li a:link, a:visited, a:actie {
        text-decoration:underline;
        color:#fff;
    }
</style>


