<template>
  <el-container>
    <el-main>
      <el-row>
        <el-col :span="12" class="login-left">
          <img src="../assets/fantasy/logo.gif" border="0" width="70%" height="70%">
        </el-col>
        <el-col :span="4" class="login-right">
          <img class="navimg" src="../assets/fantasy/enter.gif">
          <el-form  :model="loginForm" ref="loginForm" label-position="left" label-width="30%" v-loading="loading">
                <el-form-item prop="username" label="用户名">
                  <el-input type="text" v-model="loginForm.username" placeholder="请输入用户名"></el-input>
                </el-form-item>
                <el-form-item prop="password" label="密码">
                  <el-input type="password" v-model="loginForm.password" placeholder="请输入密码"></el-input>
                </el-form-item>
                <el-form-item label-width="0%" prop="captcha">
                  <el-input v-if="this.captchaItem==='true'" type="text" class="codeimg" maxlength=4 v-model="loginForm.captcha" @keyup.enter.native="submitClick" placeholder="请输入验证码"></el-input>
                  <img v-if="this.captchaItem==='true'" class="codeimg" style="cursor:pointer;" alt="点击更换" title="点击更换" @click="changeCode" v-bind:src="codeImgSrc" />
                  <el-button size="mini" @click.native.prevent="submitClick" style="">登录</el-button>
                </el-form-item>
                <a href="javascript:w()"><font color="ff0000"><b>我忘记密码了！</b></font></a>
          </el-form>

          <a href="youle/about/new.jsp"><img class="navimg" src="../assets/fantasy/new.gif"></a>
          <a href="youle/about/about.jsp"><img class="navimg" src="../assets/fantasy/about.gif"></a>
          <a href="youle/about/help.jsp"><img class="navimg" src="../assets/fantasy/help.gif"></a>
          <a href="youle/about/version.jsp"><img class="navimg" src="../assets/fantasy/version.gif"></a>


        </el-col>
        <el-col :span="8"></el-col>
      </el-row>
    </el-main>
    <el-footer>
  222
    </el-footer>
  </el-container>

</template>
<style src="../styles/fantasy-login.css"></style>
<script>
  import {postRequest} from '../utils/api'
  import {putRequest} from '../utils/api'
  import {getRequest} from '../utils/api'
  import {base} from '../utils/api'
  import {Message} from 'element-ui'
  export default{
    data(){
      return {
        // rules: {
        //   username: [{required: true, message: '', trigger: 'blur'}],
        //   password: [{required: true, message: '', trigger: 'blur'}],
        //   captcha: [
        //     {required: true, message: '', trigger: 'blur'},
        //     {min: 4, max: 4, message: '', trigger: 'blur'},
        //   ],
        // },
        // checked: true,
        loginForm: {
          username: 'sang',
          password: '123',
          captcha: ''
        },
        loading: false,
        codeImgSrc: '',
        errorinfo: '',
        captchaItem: ''
      }
    },
    methods: {
      checkInput: function(_this) {
        if (_this.loginForm.username=="") {
          Message.error('用户名不能为空！');
          return false;
        }
        if (_this.loginForm.password=="") {
          Message.error('密码不能为空！');
          return false;
        }
        // if (_this.loginForm.captcha=="") {
        //   Message.error('验证码不能为空！');
        //   return false;
        // }
        return true;
      },
      submitClick: function () {
        var _this = this;
        var result = this.$options.methods.checkInput(_this);
        if (result == true) {
          this.loading = true;
          postRequest('/login/ajaxLogin', {
            username: this.loginForm.username,
            password: this.loginForm.password,
            captcha: this.loginForm.captcha
          }).then(resp=> {
            _this.loading = false;
            if (resp.status == 200) {
              //成功
              var data = resp.data;
              this.captchaItem = data.showCode;
              if ('success' == data.result) {
                _this.$router.replace({path: '/main'});
              } else if ("null" == data.result){
                Message.error('用户名和密码不能为空！');
                _this.changeCode();
              } else if ("specialchar" == data.result){
                Message.error('用户名不能包含特殊字符！');
                _this.changeCode();
              } else if ("pwderror" == data.result){
                Message.error('用户名或密码错误！');
                _this.changeCode();
              } else if ("statuserror" == data.result){
                Message.error('用户已被禁用！');
                _this.changeCode();
              } else if ("nullcode" == data.result){
                Message.error('验证码为空或错误!');
                _this.changeCode();
              } else {
                Message.error('网络异常，请重新尝试！');
                _this.changeCode();
              }
            } else {
              //失败
              _this.$alert('登录失败!', '失败!');
            }
          }, resp=> {
            _this.loading = false;
            _this.$alert('找不到服务器⊙﹏⊙∥!', '失败!');
          });
        }
        
      },
      genTimestamp: function() {
        var time = new Date();
        return time.getTime();
      },
      changeCode: function() {
        this.codeImgSrc = base + "/login/code?t=" + this.$options.methods.genTimestamp();
      }
    },
    mounted: function() {
      //获取图片验证码
      this.changeCode();
    },
  }
</script>
<style>

</style>



