<template>
    <div class="homecontainer">
        
        <div id="main">
            <div class="ads">
                <img src="../../assets/f/豪华官邸_03.jpg" border="0">
            </div>
            <div id="sysmsgtitle">
                系統更新消息
            </div>
            <div id="sysmsg">
                系統更新消息系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息<br/>
                系統更新消息系統更新消息<br/>

            </div>
            <div id="guizititle">
                物品陈列柜
            </div>
            
            <div class="guiziTips">
                点击它就可以打开！<a href="javascript:w('queryItems.action','_blank',400,300,'yes')">家中物品查询</a>

            </div>
            <div class="guizi">
                <div class="guiziLayer">
                    <div v-show="cabinets == null" v-for="n in 30">
                        <div class="guiziItems">
                        </div>
                        <div v-if="n % 10 == 0" style="clear:both"></div>
                    </div>
                </div>
                <div class="guiziLayer">
                    <div v-show="cabinets != null" v-for="(cabinet,index) in cabinets">
                        <div class="guiziItems">
                        <a v-if="cabinet != null" :id="'cabinet_' +index" @click="openCabinet(cabinet.name, user.id, cabinet.id);">
                            <img :alt="'a' + cabinet.name" :title="'t' + cabinet.name" :src="require('../../assets/fantasy/' + cabinet.imgsrc)">
                        </a>
                        </div>
                        <div v-if="index % 10 == 9" style="clear:both"></div>
                    </div>
                </div>
            </div>
        </div>
        <div id="sidebox">
            <div id="topstitle">
                用户排行
            </div>
            <div id="tops">
                <table>
                    <tr>
                        <td>
                            <a href="#">经验榜</a>
                        </td>
                        <td>
                            <a href="#">时间榜</a>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <a href="#">本人经验排名</a>
                        </td>
                        <td>
                            <a href="#">本人时间排名</a>
                        </td>
                    </tr>
                </table>
            </div>
            <div id="movesdiv">
                <div id="movestitle">
                    用户行为
                </div>
                <div id="moves">
                    <table>
                        <tr>
                            <td>
                                <a @click="toStreet">上　　街</a>
                            </td>
                            <td>
                                <a href="#">休　　息</a>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <a href="javascript:w('showallattr.action','_blank',500,300,'yes')">个人资料</a>
                            </td>
                            <td>
                                <a href="citybuild.action">好友管理</a>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <a href="#" style="color:red">信　　箱</a>
                            </td>
                            <td>
                                <a href="javascript:w('sortitems.action?userid=${user.id }','_blank',400,300,'yes')">道具整理</a>
                            </td>
                        </tr>
                        <tr>
                            <td>
                                <a href="javascript:w('youle/icq/emotelist.jsp','_blank',350,500,'yes')">聊天动作</a>
                            </td>
                            <td>
                                <a href="#" style="color:red">新手指南</a>
                            </td>
                        </tr>
                    </table>
                </div>
            </div>

        </div>
        <el-dialog :title="cabinetName" :visible.sync="dialogTableVisible" :before-close="ai_dialog_close" >
            <div class="guizi">
                <div class="guiziLayer">
                <div v-show="items == null" v-for="n in 30">
                    <div class="guiziItems">
                    </div>
                    <div v-if="n % 10 == 0" style="clear:both"></div>
                </div>
                </div>
                <div class="guiziLayer">
                <div v-show="items != null" v-for="(item,index) in items">
                    <div class="guiziItems">
                    <a v-if="item != null" :id="'item_' +index" @click="pickitem(user.id, cityMap.id, index);">
                        <img :alt="'a' + item.name" :title="'t' + item.name" :src="require('../../assets/fantasy/' + item.imgsrc)">
                    </a>
                    </div>
                    <div v-if="index % 10 == 9" style="clear:both"></div>
                </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<style src="../../styles/fantasy-home.css"></style>
<script>
import {postRequest} from '../../utils/api'
import {base} from '../../utils/api'
export default {
  data() {
    return {
      cabinets: {},
      user: {},
      dialogTableVisible: false,
      cabinet: {},
      cabinetName: "",
      items: {},
    };
  },
  mounted: function () {
    this.initHome();
  },
  methods: {
      toStreet: function() {
        this.$router.push({path: '/main/street'});
      },
      initHome: function() {
        postRequest('/main/tohome', {}).then(resp=> {
            if (resp.status == 200) {
              //成功
              var data = resp.data;
              //alert(JSON.stringify(data))
              //alert(data.result == false)
              if (data.result == true) {
                this.user = data.user;
                this.cabinets = data.cabinets;

                this.$root.eventHub.$emit('currentUser', this.user);
              } else {
                this.$alert(data.message, '失败!');
                this.$router.replace({path: '/'});
              }
            } else {
              //失败
              this.$alert('用户非法⊙﹏⊙∥!', '失败!');
              this.$router.replace({path: '/'});
            }
          }, resp=> {
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
            this.$router.replace({path: '/'});
          });
      },
      ai_dialog_close(){
        this.dialogTableVisible = false;
      },
      //打开家中的柜子
      openCabinet(name, userId, cabinetId) {
        this.cabinetName = name;  
        this.dialogTableVisible = true;
        postRequest('/item/getItemsByCabinetId', {
            userId: userId,
            cabinetId: cabinetId
        }).then(resp=> {
            if (resp.status == 200) {
              //成功
              var data = resp.data;
              //alert(JSON.stringify(data))
              //alert(data.result == false)
              if (data.result == true) {
                this.items = data.items;

              } else {
                this.$alert(data.message, '失败!');
                this.$router.replace({path: '/'});
              }
            } else {
              //失败
              this.$alert('用户非法⊙﹏⊙∥!', '失败!');
              this.$router.replace({path: '/'});
            }
          }, resp=> {
            this.$alert('用户非法⊙﹏⊙∥!', '失败!');
            this.$router.replace({path: '/'});
          });
      }
    },
    beforeDestroy: function() {
    }
}
</script>

<style>

</style>


